const parseTime = (time) => {
  const date = new Date(time);
  const year = date.getUTCFullYear();
  const month = date.getMonth() + 1;
  const day = date.getDate();
  return `${year}ÎÖÑ ${month}Ïõî ${day}Ïùº`;
};

